/*eslint-disable */
import EditorBlock from '@/components/EditorBlock/index.vue'
import Drawflow from 'drawflow'
import BlocksMenu from "@/components/BlocksMenu/index.vue"
import AdminLayout from "@/layouts/Admin/index.vue"
import { defineComponent } from "vue";
import * as Vue from "vue";
import { Block } from "@/interfaces/Block";
import routes from "@/routes";
import EventEmitter from "@/util/EventEmitter";
import Loading from "vue3-loading-overlay";
import "vue3-loading-overlay/dist/vue3-loading-overlay.css";
import { useStore } from "vuex";
import useVuelidate from "@vuelidate/core";
import { required, minLength } from "@vuelidate/validators";

const mobile_item_selec: any = null;
const mobile_last_move: any = null;
const blocks: Block[] = [];
const botId: any = null;
const editorId = "botoEditor";
const fieldsContext: any = [];

export default defineComponent({
  name: "App",
  setup(){
    const store = useStore();
    return {
      store,
      v$: useVuelidate(),
    }
  },
  validations () {
    return {
      botName: {
        required,
        minLength: minLength(5)
      }
    }
  },
  data() {
    return {
      editor: null as any,
      mobile_item_selec,
      mobile_last_move,
      transform: "",
      open: false,
      blocks,
      saveError: false,
      saveErrorText: "",
      saveSuccess: false,
      saveLoading: false,
      botId,
      botName: "",
      botFrequency: 60,
      isLoading: false,
      fieldsContext,
    };
  },
  components: {
    BlocksMenu,
    AdminLayout,
    EditorBlock,
    Loading,
  },
  methods: {
    positionMobile(ev: any) {
      this.mobile_last_move = ev;
    },
    allowDrop(ev: any) {
      ev.preventDefault();
    },
    drag(ev: any) {
      if (ev.type === "touchstart") {
        this.mobile_item_selec = ev.target
          .closest(".drag-drawflow")
          .getAttribute("data-node");
      } else {
        ev.dataTransfer.setData("node", ev.target.getAttribute("data-node"));
      }
    },
    drop(ev: any) {
      if (ev.type === 'touchend') {
        const x = this.mobile_last_move!.touches[0].clientX;
        const y = this.mobile_last_move!.touches[0].clientY;
        const elFromPoint = document.elementFromPoint(x, y);
        const parentdrawflow = elFromPoint!.closest("#" + editorId);
        if (parentdrawflow != null) {
          this.addNodeToDrawFlow(
            this.mobile_item_selec,
            JSON.parse(this.mobile_item_selec),
            x,
            y
          );
        }
        this.mobile_item_selec = "";
      } else {
        ev.preventDefault();
        const id = ev.dataTransfer.getData("node");
        const data = ev.dataTransfer.getData("data");
        this.addNodeToDrawFlow(id, JSON.parse(data), ev.clientX, ev.clientY);
      }
    },
    clearBot() {
      this.editor.clear();
      this.initContext();
    },
    async saveBot() {
      try {
        if (!this.v$.$invalid) {
          this.saveLoading = true;
          this.saveSuccess = false;
          this.saveError = false;
          this.saveErrorText = "";
          const exportdata = this.editor.export();
          const exportJson = exportdata.drawflow.Home;
          const jsonData = {
            name: this.botName,
            json: exportJson,
            frequency: this.botFrequency,
          };
          console.log("exportdata", exportdata, jsonData, JSON.stringify(jsonData));
          let method = (this.botId) ? "PUT" : "POST";
          let url = "https://botoapp.io/api/bots/";
          if (this.botId) url = `${url}${this.botId}/`;
          const requestOptions = {
            method,
            headers: {
              "Content-Type": "application/json",
              Authorization: `Token ${this.store!.getters.localStorageAuthToken}`,
            },
            body: JSON.stringify(jsonData)
          };
          const response = await fetch(url, requestOptions);
          if (!response.ok) {
            throw new Error(`An error has occured: ${response.status}`);
          }
          const data = await response.json();
          this.botId = data.id;
          this.botName = data.name;
          this.saveLoading = false;
          this.saveSuccess = true;
          this.cleanSave();
        }
      } catch (e) {
        this.saveLoading = false;
        this.saveSuccess = false;
        this.saveError = true;
        this.saveErrorText = e;
        this.cleanSave();
      }
    },
    cleanSave () {
      const vm = this;
      setTimeout(function(){ 
        vm.saveLoading = false;
        vm.saveSuccess = false;
        vm.saveError = false;
        vm.saveErrorText = "";
      }, 3000);
    },
    getPreviousNodes (node: number, nodes?: []) {
      const data = this.editor.drawflow.drawflow["Home"].data;
      const inputs = data[node].inputs;
      let result: any = [];
      if (nodes) result = nodes;
      for (let key in inputs) {
        const input = inputs[key];
        for (let x = 0;x < input.connections.length; x++) {
          const connections = input.connections;
          result.push(connections[x].node);
          this.getPreviousNodes(connections[x].node, result);
        }
      }
      return Array.from(new Set(result));
    },
    getForwardNodesToBeUpdated (node: number, nodes?: []) {
      const data = this.editor.drawflow.drawflow["Home"].data;
      const outputs = data[node].outputs;
      let result: any = [];
      if (nodes) result = nodes;
      for (let key in outputs) {
        const output = outputs[key];
        for (let x = 0;x < output.connections.length; x++) {
          const connections = output.connections;
          result.push(connections[x].node);
          this.getForwardNodesToBeUpdated(connections[x].node, result);
        }
      }
      return Array.from(new Set(result));
    },
    async getBlocks () {
      const response = await fetch("https://botoapp.io/api/blocks/", {
        cache: "no-store",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Token ${this.store.getters.localStorageAuthToken}`,
        },
      });
      const data = await response.json();
      return data;
    },
    cancelSaveBot () {
      this.botId = null;
      this.botName = "";
      this.editor.clear();
      this.initContext();
      routes.push({ name: "home.editor", params: {} });
    },
    async getBot (id: string) {
      const response = await fetch(`https://botoapp.io/api/bot/${id}/`, {
        cache: "no-store",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Token ${this.store.getters.localStorageAuthToken}`,
        },
      });
      const data = await response.json();
      return data;
    },
    onFrequencyChange (newFrequency: number){
      this.botFrequency = newFrequency;
    },
    initContext() {
      this.editor.drawflow.drawflow["Home"].context = {};
      this.editor.drawflow.drawflow["Home"].context.nodes = {};
    },
    initDiagram(){
      const id = document.getElementById(editorId);
      this.editor = null;
      // @ts-expect-error test
      this.editor = new Drawflow(id, Vue);
      this.editor!.start();
    },
    addNodeToDrawFlow(id: string, data: any, pos_x: number, pos_y: number) {
      if (this.editor.editor_mode === "fixed") {
        return false;
      }
      pos_x =
        pos_x *
          (this.editor.precanvas.clientWidth /
            (this.editor.precanvas.clientWidth * this.editor.zoom)) -
        this.editor.precanvas.getBoundingClientRect().x *
          (this.editor.precanvas.clientWidth /
            (this.editor.precanvas.clientWidth * this.editor.zoom));
      pos_y =
        pos_y *
          (this.editor.precanvas.clientHeight /
            (this.editor.precanvas.clientHeight * this.editor.zoom)) -
        this.editor.precanvas.getBoundingClientRect().y *
          (this.editor.precanvas.clientHeight /
            (this.editor.precanvas.clientHeight * this.editor.zoom));
        const componentId = data.id;
        const edata = {};
        const node = this.editor.addNode(
          componentId,
          data.inputs,
          data.outputs,
          pos_x,
          pos_y,
          "class",
          edata,
          componentId,
          "vue"
        );
        this.editor.drawflow.drawflow["Home"].context.nodes[componentId] = node;
    },
    sendBotContext(data: any) {
      for (let key in data) {
        const destination = data[key].id;
        const forwardNodes: any = this.getForwardNodesToBeUpdated(destination);
        const prevNodes: any = this.getPreviousNodes(parseInt(destination));
        EventEmitter.emitter.emit(`editor-block-context-update-${destination}`, this.fieldsContext, prevNodes);
        for (let x = 0; x < forwardNodes.length; x++) {
          EventEmitter.emitter.emit(`editor-block-context-update-${forwardNodes[x]}`, this.fieldsContext, prevNodes);
        }
      }
    },
    sendContext(output: any) {
      const destination = output.input_id;
      const forwardNodes: any = this.getForwardNodesToBeUpdated(destination);
      const prevNodes: any = this.getPreviousNodes(parseInt(destination));
      EventEmitter.emitter.emit(`editor-block-context-update-${destination}`, this.fieldsContext, prevNodes);
      for (let x = 0; x < forwardNodes.length; x++) {
        EventEmitter.emitter.emit(`editor-block-context-update-${forwardNodes[x]}`, this.fieldsContext, prevNodes);
      }
    }
  },
  async mounted() {
    try {
      if (this.editor) this.editor.clear();
      const botoId = this.$route.params.id;
      // Get Blocks
      const blocks = await this.getBlocks();
      this.blocks = blocks.results.filter(function (el: any) {
        return el.published === true;
      });
      // Editor
      this.initDiagram();
      this.initContext();

      // Register Blocks
      for (let x=0;x < this.blocks.length;x++) {
        const b = this.blocks[x];
        const props = b;
        const options = {};
        this.editor.registerNode(b.id, EditorBlock, props, options);
      }

      if (botoId) {
        this.botId = botoId.toString();
        this.isLoading = true;
        const bot = await this.getBot(this.botId);
        this.editor.import({
          drawflow: {
            Home: {
              context: {
                nodes: bot.json.context.nodes,
              },
              data: bot.json.data,
            },
          }
        }
        );
        setTimeout(function () {
          vm.sendBotContext(bot.json.data);
        }, 40);
        this.botName = bot.name;
        this.botFrequency = bot.frequency;
        this.isLoading = false;
      }

      const elements = document.getElementsByClassName("drag-drawflow");
      for (let i = 0; i < elements.length; i++) {
        elements[i].addEventListener("touchend", this.drop, false);
        elements[i].addEventListener("touchmove", this.positionMobile, false);
        elements[i].addEventListener("touchstart", this.drag, false);
      }
      const vm = this;
      EventEmitter.emitter.on("block-properties-change", function (block: string, data: any) {
        const json = vm.editor.drawflow.drawflow["Home"];
        if (!json.data[block]) json.data[block] = { data: {} };
        json.data[block].data[data.id] = data.value;
      });
      EventEmitter.emitter.on("editor-block-data-request", function (cid: number,id: number, iuid: string) {
        const json = vm.editor.drawflow.drawflow["Home"];
        const eid = cid ? cid : json.context.nodes[id];
        EventEmitter.emitter.emit(`editor-block-data-response-${iuid}`, json.data[eid]);
      });
      EventEmitter.emitter.on("editor-block-data-ctx-add", function (id: number, vars: []) {
        if (id) {
          for (let key in vars) {
            let item: any = vars[key];
            const value = `@${id}.${item.slug}`;
            if (!vm.fieldsContext.find((o: { value: string; }) => o.value === value)) {
              vm.fieldsContext.push(
                { value, text: item.name, title: item.description }
              );
            }
          }
        }
      });

      // Editor events
      this.editor.on("connectionCreated", this.sendContext);
      this.editor.on("connectionRemoved", this.sendContext);
    } catch (e) {
      console.error(e);
      this.isLoading = false;
    }
  } 
});
