import { defineComponent } from "vue";
import BlockProperties from "@/components/BlockProperties/index.vue";
import EventEmitter from "@/util/EventEmitter";
import { v4 as uuidv4 } from "uuid";

export default defineComponent({
  name: "EditorBlockComponent",
  props: {
    id: Number,
    name: String,
    description: String,
    logo_class: String,
    output_vars: [],
    parameters: [],
    nodeData: Object,
  },
  components: {
    BlockProperties,
  },
  setup() {
    return {
      uiid: uuidv4(),
    };
  },
  data() {
    return {
      showProperties: false,
      context: {} as any,
      botContext: {} as any,
      uid: this.$props.nodeData ? this.$props.nodeData.id : null,
    };
  },
  mounted() {
    const uiid = this.uiid;
    const id = this.id;
    const uid = this.uid;
    console.log("this.$props.nodeData", this.$props);
    setTimeout(function () {
      EventEmitter.emitter.emit("editor-block-data-request", uid, id, uiid);
    }, 40);
    EventEmitter.emitter.on(`editor-block-data-response-${uiid}`, this.setCtx);
  },
  watch: {
    uid: {
      immediate: true,
      handler(val) {
        // eslint-disable-next-line @typescript-eslint/no-this-alias
        const vm = this;
        EventEmitter.emitter.emit("editor-block-data-ctx-add", this.uid, this.output_vars);
        EventEmitter.emitter.on(
          `editor-block-context-update-${val}`,
          function (context: any, nodes: any) {
            console.log("context", context, "nodes", nodes);
            vm.botContext = vm.filterCtx(context, nodes);
            console.log("vm.filterCtx(context, nodes)", vm.filterCtx(context, nodes));
          }
        );
      },
    },
  },
  methods: {
    filterCtx(context: any, nodes: any) {
      const result: any = [];
      for (let x = 0; x < context.length; x++) {
        if (nodes.indexOf(context[x].value.substring(1, 2)) > -1) {
          result.push(context[x]);
        }
      }
      return result;
    },
    setCtx(ctx: any) {
      console.log("setCtx", ctx);
      if (ctx) {
        this.context = {};
        for (const key in ctx.data) {
          this.context[key] = ctx.data[key];
        }
        if (!this.uid) {
          this.uid = ctx.id;
        } else {
          EventEmitter.emitter.emit("editor-block-data-ctx-add", this.uid, this.output_vars);
        }
      }
      console.log("nodeData", this.nodeData);
    },
    toggleSettings(st: boolean) {
      if (st) {
        this.showProperties = st;
      } else {
        this.showProperties = !this.showProperties;
      }
    },
  },
});
